import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect:'/login',
    component: () => import('../views/loginView.vue')
  },
  {
    path: '/stu',
    name: 'stu',
    component: () => import('../views/stuView.vue')
  },
  {
    path: '/room',
    name: 'room',
    component: () => import('../views/roomView.vue')
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/loginView.vue')
  }
]

const router = new VueRouter({
  
  routes
})

export default router
